body {
  background-color: rgba(0, 0, 0, 0.884);
  color: #03C4A1;
  font-family: 'Open Sans', Tahoma, Geneva, Verdana, sans-serif
}

.container {
  display: grid;
  min-width: 320px;
  margin: auto;
  grid-template-columns: 100%;
  grid-template-rows: 10% 75% 15%;
  grid-template-areas: 
  "navbar"
  "aside aside aside"
  "nav main main"
  "footer";
  justify-content: stretch;
  align-items: stretch;
  grid-gap: 8px;
}

a {
  color: #C62A88;
  font-weight: bold;
  text-decoration: none;
  font-size: larger;
}

a:hover {
  color: #c7619e;
}

.card {
  background-color: black;
  border-color: #03C4A1;
  border-width: 5px;
}

.fa {
  font-size: 40px;
}

.navbar {
  border-bottom: #03C4A1;
  border-bottom-style: solid;
  border-width: 6px;
}

.navbar h1 {
  font-weight: bold;
}

.landing {
  font-family: 'Permanent Marker', 'Open Sans', Tahoma;
  padding-bottom: 20vh;
  padding-top: 20vh;
}

.landing h1 {
  font-size: 80px;
}

.landing h2 {
  font-size: 60px;
}

.about .section{
  padding-bottom: 8vh;
  padding-top: 3vh;
  padding-left: 10vh;
  padding-right: 10vh;    
}

.para {
  padding-left: 80px;
  padding-right: 80px;
  font-size: larger;
}

.linkP {
  font-size: 36px;
}

.icons {
  margin-left: 8px;
}

.details {
  display: none;
  position: absolute;
  top: 35px;
  left: 0;
  width: 100%;
  height: 75%;
}

.card:hover .details {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-weight: bolder;
  font-size: 18px;
  text-align: center;
}

.card:hover .card-img {
  opacity: .1;
}